
@font-face {
    font-family: "disket-regular";
    src: url("/fonts/Disket-Mono-Regular.ttf") format("truetype");
      }
 @font-face {
        font-family: "disket-bold";
        src: url("/fonts/Disket-Mono-Bold.ttf") format("truetype");
          }
body {
    background-color: black;
    font-family: disket-regular;
    color: white;
    
}
a:link {
  text-decoration: none;
  
}
#pcontainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  
}

#ad{
  display: flex;
  justify-content: center;
  animation: opacity 2s ease-in;
  
}

#p1 {
  /* opacity: 0; */
  font-size: 50pt;
  display: flex;
  padding: 10% 20% 0% ;
  align-items: left;
  animation: opacity 2s ease-in;
  
}

#p2 {
  /* opacity: 0; */
  display: flex;
  padding: 0% 20% 0% ;
  align-items: left;
  animation: opacity 2s ease-in;
} 
.objcont {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: auto;
  height: 100vh;
  padding: 0% 20% 0% ;
  align-items: left;
}
  

#vid2 {

  width: 100vw;
  height: 100vh;
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;
  z-index: -1;
  
}
#cont {
  
  margin: 20%;
  color: white;
  display: flex;
  justify-content: center;
  text-align: center;
  animation: blink2 .3s infinite ;
  display: none;
}

#cont:hover {
  background: none;
  color: white;
  border: 1px solid white;
  animation: none;
}
.menu {
  display: flex;
  justify-content:space-between;
  margin: 10pt;
  font-size: 10pt;
  
}

#back1 {
    width: 100%;
    height: auto;
    display: flex;
    font-family: 'Courier New', Courier, monospace;
    font-size: 200;
    text-align: center;
    position: top, left;
}
#back2 {
    width: 100%;
    height: auto;
    font-family: 'Courier New', Courier, monospace;
    font-size: 200;
    text-align: center;
    position: top, left;
}

#vid1 {
    position: fixed;
    top: 0;
    left: 0;
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
    z-index: -1;
}

#btn1 {
    display: flex;
    justify-content: center;
    margin: 10% auto;
    text-transform: uppercase;
    background: #000;
    cursor: pointer;
    font-size: 100pt;
    animation: blink .3s infinite linear;
    z-index: 0;

  
    
    
}

#back1 {
  position: fixed;
}

#back2 {
  position: fixed;
}

button {

    font-family: disket-regular;
    color: white;
    background: none;
    border: 5px solid white;
    
   
   
}

button:hover {
    color: white;
    background: #ED6C6C;
    border: 5px solid #ED6C6C;

    
}


.ptxt {
   

  white-space: nowrap;


  justify-content: center;
  text-align: center;
  
  font-size: 10pt;
  display: flex;
  flex-direction: column;
  color: white;
  font-family: disket-regular;
  display: flex;
  overflow: hidden;
  
  z-index: 1;
  
 
  
  
}

.container {
  opacity: 0;
  transition: opacity 2s ease-in;
}

.container.fade {
  opacity: 1;
  z-index: -1;
}

.fbtn {
  
  border: 2px solid ;
}

.vol {
  
  border: 2px solid ;
}

.fbtn:hover {
  color: #000;
  border: 2px solid #ED6C6C;}

.vol:hover {
  color: #000;
  border: 2px solid #ED6C6C;}

#bottom {
   
    overflow: hidden;
   
    animation: typing 3s steps(20,end);
}
@media (min-aspect-ratio: 16/9) {
    video {
      width: 100%;
      height: auto;
    }
  }
  @media (max-aspect-ratio: 16/9) {
    video {
      width: auto;
      height: 100%;
    }
  }

  @keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
   
  @keyframes opacity{
    0%{
      opacity: 0%;
    }
    100% {
      opacity: 100%;
    }
  }
  @keyframes blink{
    0%{}
    100% {
      
      color: black;
    }
  }
  @keyframes blink2{
    0%{}
    100% {
      background-color: white;
      color: black;
    }
  }
  @keyframes on{
    0%{
      
    }
    100% {
      opacity: 100%;
    }
  }
  @keyframes typing {
    from { width: 100% }
    to { width: 0 }
  }